  width: 7px;
  height: 7px;
}
#root {
  background-image: linear-gradient(270deg,#1b1429,#140f23);
  width: 100vw;
  position: absolute;
  display: grid;
  grid-auto-flow: row;
  /* grid-auto-rows: 95vh; */
  overflow: auto;
  overflow-x: hidden;
}

